/* oswald-regular - latin_cyrillic from google-webfonts-helper*/
@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src:
    url("../fonts/oswald-400.woff2") format("woff2"),
    url("../fonts/oswald-400.woff") format("woff");
}

@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src:
    url("../fonts/oswald-500.woff2") format("woff2"),
    url("../fonts/oswald-500.woff") format("woff");
}
