/* oswald-regular - latin_cyrillic from google-webfonts-helper*/
@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswald-400.woff2") format("woff2"), url("../fonts/oswald-400.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswald-500.woff2") format("woff2"), url("../fonts/oswald-500.woff") format("woff");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}
.page {
  height: 100%;
}
.body {
  min-width: 320px;
  margin: 0;
  height: 100%;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #000000;
  background-color: #FFFFFF;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
}
/* * {
  outline: 1px solid lightsalmon;
}*/
.header {
  min-width: 100%;
  margin-left: auto;
  margin-right: auto;
  background-color: #FFFFFF;
  border-bottom: 1px solid #D9D9D9;
  /*outline: 2px solid red;
    outline-offset: -2px;*/
}
.header--catalog,
.header--form {
  border-bottom: 1px solid #D9D9D9;
}
.header__logo {
  width: 100%;
  height: 65px;
  padding-left: 20px;
  padding-right: 20px;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #D9D9D9;
  /* outline: 4px dashed green;
  outline-offset: -4px;*/
}
.header__logo-tablet,
.header__logo-desktop {
  display: none;
}
@media (min-width: 768px) {
  .header {
    width: 100%;
    height: 113px;
    border-bottom: none;
    display: flex;
    justify-content: space-between;
  }
  .header--catalog,
  .header--form {
    width: 100%;
    display: flex;
    justify-content: space-between;
    border-bottom: none;
  }
  .header__logo {
    width: 23%;
    height: 108px;
    margin-left: 30px;
    border-bottom: none;
  }
  .header__logo-mobile,
  .header__logo-cat-energy,
  .header__logo-desktop {
    display: none;
  }
  .header__logo-tablet {
    display: block;
    border-bottom: none;
  }
}
@media (min-width: 1440px) {
  .header {
    height: 90px;
    padding-top: 70px;
  }
  .header--index {
    width: 100%;
    height: 140px;
    padding-top: 0;
  }
  .header--catalog {
    height: 188px;
    display: flex;
  }
  .header--form {
    padding-top: 0;
    display: flex;
  }
  .header__logo {
    width: 50%;
    margin-left: 110px;
    margin-right: 0;
    justify-content: start;
    z-index: 5;
  }
  .header__logo-mobile,
  .header__logo-cat-energy,
  .header__logo-tablet {
    display: none;
  }
  .header__logo-desktop {
    display: block;
    margin-top: 65px;
    z-index: 10;
    /*outline: 1px solid green;*/
  }
}
.main-nav {
  min-width: 320px;
}
.main-nav__list {
  list-style: none;
  text-align: center;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  display: flex;
  flex-direction: column;
}
.main-nav__item {
  margin-top: 24px;
  margin-bottom: 24px;
}
.main-nav__link {
  text-decoration: none;
  font-family: "Oswald", Arial, sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  color: #000000;
}
.main-nav__link:hover {
  color: rgba(0, 0, 0, 0.6);
}
.main-nav__link:active {
  color: rgba(0, 0, 0, 0.8);
}
