@media (min-width: 768px) {
  .main-nav {
    width: 57%;
    margin-right: 30px;
  }
  .main-nav__list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 108px;
    margin: 0;
  }
  .main-nav__close {
    display: none;
  }
  .main-nav__link {
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
  .main-nav__link--current {
    border-bottom: 2px solid #68B738;
  }
  .main-nav__link-form {
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    text-transform: uppercase;
    color: #000000;
  }
  .main-nav__item {
    margin: 0;
  }
}
@media (min-width: 1440px) {
  .main-nav {
    width: 50%;
    margin-left: 0;
    margin-right: 110px;
    margin-top: 0;
    z-index: 5;
  }
  .main-nav__item:first-child {
    margin-left: 130px;
  }
  .main-nav__list {
    margin-top: 34px;
  }
  .main-nav__link {
    font-size: 20px;
    line-height: 30px;
  }
  .main-nav__link--current {
    border-bottom: 2px solid #68B738;
    height: 15px;
  }
  .main-nav__link--index {
    color: #FFFFFF;
  }
  .main-nav__link--current-index {
    border-bottom: 2px solid #FFFFFF;
  }
  .main-nav__link--catalog {
    color: #111111;
  }
}
