}
.programms__wrapper {
  padding-left: 20px;
  padding-bottom: 15px;
  background-color: #F2F2F2;
  position: relative;
}
.programms__title {
  font-size: 24px;
  line-height: 37px;
  font-family: "Oswald", Arial, sans-serif;
  font-weight: normal;
  display: block;
  text-align: left;
  text-transform: uppercase;
