.form__label--radio::before {
  content: " ";
  position: absolute;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #FFFFFF;
  border: 1px solid #CDCDCD;
  left: -45px;
}
.form__label--radio::after {
  content: " ";
  position: absolute;
  width: 13.5px;
  height: 13.5px;
  border-radius: 50%;
  background-color: #68B738;
  left: -29px;
  top: 50%;
  transform: translate(-50%, -50%);
  display: none;
}
.form__label--checkbox {
  margin-left: 46px;
